<template>
    <section class="content">
        <div class="container-fluid">
            <div class="row">
                <div class="col-12">
                    <div class="card">
                        <div class="card-header">
                            <h3 class="card-title">Combined Expense Expenses Report</h3>
                            <div class="card-tools"></div>
                        </div>

                        <div class="card-body p-2">
                            <form autocomplete="off" @submit.prevent="getResults()">
                                <div class="form-row">
                                    <div class="col col-2">
                                        <div class="form-check">
                                            <label class="form-check-label">
                                                <input type="radio" class="form-check-input" name="optradio"
                                                    v-model="search.report_type" value="site wise">Site Wise
                                            </label>
                                        </div>
                                        <div class="form-check">
                                            <label class="form-check-label">
                                                <input type="radio" class="form-check-input" name="optradio"
                                                    v-model="search.report_type" value="machine wise">Machine Wise
                                            </label>
                                        </div>
                                    </div>
                                    <div class="col col-2">
                                        <v-select label="site_name" :reduce="(option) => option.id" :options="search_sites"
                                            placeholder="Select Site..." v-model="search.site_id" name="site_id">
                                        </v-select>
                                    </div>
                                    <div class="col col-2 ">
                                        <datetime value-zone="Asia/Kolkata" v-model="search.date_from"
                                            input-class="form-control" placeholder="Created from date" />
                                    </div>
                                    <div class="col col-2 ">
                                        <datetime value-zone="Asia/Kolkata" v-model="search.date_to"
                                            input-class="form-control" placeholder="Created to date" />
                                    </div>

                                    <div class="col col-3">
                                        <v-select label="name" :reduce="(option) => option.id" :options="categories"
                                            placeholder="Choose Category ..." v-model="search.category_id">\
                                        </v-select>
                                    </div>
                                    <div class="col col-3">
                                        <v-select label="name" :reduce="(option) => option.id" :options="sub_categories"
                                            placeholder="Choose description ..." v-model="search.sub_category_id">
                                        </v-select>
                                    </div>
                                    <div class="col col-2">
                                        <v-select label="name" :get-option-label="getLabel" :reduce="(option) => option.id"
                                            :options="search_machines" placeholder="Enter machines ..."
                                            v-model="search.machine_id" name="machine_id">
                                        </v-select>
                                    </div>
                                    <!--
                                    <div class="col col-2">
                                        <v-select label="ticket_no" :reduce="(option) => option.id"
                                            :options="search_tickets" placeholder="Enter ticket ..."
                                            v-model="search.ticket_id" name="ticket_id">
                                        </v-select>
                                    </div> -->


                                    <div class="col ">
                                        <button type="submit" class="btn btn-success">
                                            Search
                                        </button>
                                        <button type="button" class="btn btn-info ml-2" @click="reset_filter()">
                                            Reset
                                        </button>
                                        <download-excel class="btn btn-warning ml-2"
                                            v-if="search.report_type == 'machine wise'" :fetch="export_csv"
                                            :fields="json_fields" :before-generate="startDownload"
                                            :before-finish="finishDownload" worksheet="My Worksheet" type="csv"
                                            name="machine_expenses.csv">
                                            <span class="labelText">
                                                <i class="fa fa-file-excel-o" aria-hidden="true"></i>
                                                Export as CSV</span>
                                        </download-excel>

                                        <download-excel v-else class="btn btn-warning ml-2" :fetch="export_csv"
                                            :fields="json_site_fields" :before-generate="startDownload"
                                            :before-finish="finishDownload" worksheet="My Worksheet" type="csv"
                                            name="site_expenses.csv">
                                            <span class="labelText">
                                                <i class="fa fa-file-excel-o" aria-hidden="true"></i>
                                                Export as CSV</span>
                                        </download-excel>

                                    </div>
                                </div>
                            </form>
                        </div>

                        <!-- /.card-header -->
                        <div class="card-body table-responsive p-0 ticketTable">
                            <table class="table table-hover table-striped" v-if="Tickets && Tickets.records">
                                <thead>
                                    <tr>
                                        <th>{{ search.report_type == 'machine wise' ? 'Machine Name' : 'Site Name' }}</th>
                                        <th style="width: 25px;" v-if="search.report_type == 'machine wise'">Make</th>
                                        <th style="width: 25px;" v-if="search.report_type == 'machine wise'">Model</th>
                                        <th>{{ search.report_type == 'machine wise' ? 'FAR No' : 'Site ID' }}</th>
                                        <th v-if="search.report_type == 'machine wise'">DOP</th>
                                        <th v-if="search.report_type == 'machine wise'">Purchase Price</th>
                                        <th v-if="search.report_type == 'machine wise'" style="text-align: right;">PDV</th>
                                        <th>No of Ticket</th>
                                        <th>Breakdown </th>
                                        <th>Preventive</th>
                                        <th>Major</th>
                                        <th>Minor</th>
                                        <th style="text-align: right;">Material Expenses </th>
                                        <th style="text-align: right;">Service Expenses</th>
                                        <th style="text-align: right;">Total Expenses</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <template v-for="(ticket,xx) in Tickets.records.data">
                                        <tr :key="ticket.id" :class="ticket.row_color">
                                            <td style="width: 25px;"> {{ search.report_type == 'machine wise' ? ticket.name : ticket.site_name }}</td>
                                            <td style="width: 25px;" v-if="search.report_type == 'machine wise'"> {{ ticket.make_name }}</td>
                                            <td v-if="search.report_type == 'machine wise'"> {{ ticket.model_name }}</td>
                                            <td style="min-width: 130px;"> {{ search.report_type == 'machine wise' ? ticket.far_no : ticket.site_id }}
                                                <span class="pl-2" style="float: right;"
                                                    @click="get_tickets(ticket,xx);sub_index=(xx+1)"><i class="fa fa-plus bg-dark"></i></span>
                                            </td>
                                            <td align="right" v-if="search.report_type == 'machine wise'">{{ ticket.purchase_date |myDate }}</td>
                                            <td align="right" v-if="search.report_type == 'machine wise'">{{ ticket.purchase_price | toCurrency }}</td>
                                            <td align="right" v-if="search.report_type == 'machine wise'">{{ ticket.depreciated_amount | toCurrency }}</td>
                                            <td align="center">{{ ticket.total_tickets }}</td>
                                            <td align="center">{{ ticket.total_Breakdown }}</td>
                                            <td align="center">{{ ticket.total_Periodic }}</td>
                                            <td align="center">{{ ticket.total_major }}</td>
                                            <td align="center">{{ ticket.total_minor }}</td>
                                            <td align="right">{{ ticket.material_expenses | toCurrency }}</td>
                                            <td align="right">{{ ticket.service_expenses | toCurrency }}</td>
                                            <td align="right">{{ (ticket.total_expenses) | toCurrency }}</td>
                                        </tr>

                                        <tr v-if="sub_tickets && (sub_index==xx+1)" :key="'sk' + ticket.id">
                                            <td colspan="10" align="center">
                                                <table>
                                                    <thead>
                                                        <tr>
                                                            <th>Machine Name</th>
                                                            <th>{{ search.report_type == 'machine wise' ? 'Site Name' :'FAR No' }} </th>
                                                            <th>Operator Name</th>
                                                            <th>Ticket No</th>
                                                            <th>DOM</th>
                                                            <th>Material Expenses </th>
                                                            <th>Material PO</th>
                                                            <th>Service Expenses</th>
                                                            <th>Service PO</th>
                                                            <th>Total Expenses</th>
                                                            <th>Type</th>
                                                            <th>Fault</th>
                                                            <th>TAT</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr :key="ticket.id + '-' + ix" v-for="(item, ix) in sub_tickets"
                                                            v-if="sub_tickets">
                                                            <td> {{ item.machine.name }}</td>
                                                            <td> {{ search.report_type == 'machine wise' ? item.site.site_name : item.machine.far_no}}</td>
                                                            <td align="center">{{item.machine.machine_operators != null ? item.machine.machine_operators.operator_name : '' }}</td>
                                                            <td>{{ item.ticket_no }}</td>
                                                            <td>{{ item.actual_date|myDate }}</td>
                                                            <td align="right">{{ item.actual_material_cost | toCurrency }}</td>
                                                            <td align="center">
                                                                <a :href="it.file" target="_blank" v-for="(it,c) in item.actual_estimated_material_files"><i class="fa fa-file-pdf" aria-hidden="true" style="color:red"></i> &nbsp;</a>
                                                            </td>
                                                            <td align="right">{{ item.actual_service_cost | toCurrency }}</td>
                                                            <td align="center">
                                                                <a :href="it.file" target="_blank" v-for="(it,c) in item.actual_estimated_service_files"><i class="fa fa-file-pdf" aria-hidden="true" style="color:red"></i> &nbsp;</a>
                                                            </td>
                                                            <td align="right">{{ (item.actual_material_cost + item.actual_service_cost) |
                                                                toCurrency }}</td>
                                                                <td>
                                                                <span v-if="item.ticket_type =='Breakdown Maintenance'" class="red">Breakdown</span>
                                                                <span v-if="item.ticket_type =='Periodic Maintenance'">Preventive</span>
                                                                </td>
                                                                <td><span :class="{red:item.complaint_nature=='Major'}">{{ item.complaint_nature }}</span></td>
                                                                <td>
                                                                    {{ item.status=='Closed' ? item.tat:'-' }}
                                                                </td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </td>
                                        </tr>
                                    </template>

                                </tbody>
                                <tfoot>
                                    <tr class="table-dark">
                                        <td v-if="search.report_type == 'machine wise'" colspan="11"><strong>Total</strong></td>
                                        <td v-if="search.report_type == 'machine wise'"></td>
                                        <td v-else colspan="7"><strong>Total</strong></td>
                                        <td align="right"><span v-if="Tickets && Tickets.actual_material_cost">{{
                                            Tickets.actual_material_cost | toCurrency }}</span></td>
                                        <td align="right"><span v-if="Tickets && Tickets.actual_service_cost">{{
                                            Tickets.actual_service_cost | toCurrency }}</span></td>
                                        <td align="right">{{ (Tickets.actual_material_cost + Tickets.actual_service_cost) | toCurrency }}
                                        </td>
                                    </tr>
                                </tfoot>
                            </table>
                        </div>
                        <!-- /.card-body -->
                        <div class="card-footer" v-if="Tickets && Tickets.records">
                            <pagination :data="Tickets.records" @pagination-change-page="getResults"></pagination>
                        </div>
                    </div>
                    <!-- /.card -->
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { MonthPicker } from 'vue-month-picker'
import { MonthPickerInput } from 'vue-month-picker'
export default {
    name: "CombinedExpenses",
    components: {
        MonthPicker,
        MonthPickerInput
    },
    data() {
        return {
            sub_tickets:'',
            sub_index:'',
            pmModalData: "",
            created_users: [],
            search_machines: [],
            search_sites: [],
            search_tickets: [],
            search: {
                page: 1,
                machine_id: "",
                site_id: "",
                category_id: "",
                sub_category_id: "",
                date_from: "",
                date_to: "",
                report_type: "machine wise",
            },
            categories: [],
            sub_categories: [],
            sites: [],
            selected_machine: "",
            users: [],
            machines: [],
            Tickets: {},
            clearEmittedText: '',
            default_month: '',
            json_fields: {
                "Machine Name": "name",
                "Machine Make": "make_name",
                "Machine Model": "model_name",
                "FAR No": "far_no",
                "Purchase Date": "purchase_date",
                "Purchase Price": "purchase_price",
                "No of Ticket":"total_tickets" ,
                "Breakdown":"total_Breakdown" ,
                "Preventive":"total_Periodic" ,
                "Major" :"total_major",
                "Minor":"total_minor",
                "Actual Material Expenses": "material_expenses",
                "Actual Service Expenses": "service_expenses",
                "Total Expenses": "total_expenses",
                "PDV": "depreciated_amount",
            },
            json_site_fields: {
                "Site Name": "site_name",
                "Site ID": "site_id",
                "No of Ticket":"total_tickets" ,
                "Breakdown":"total_Breakdown" ,
                "Preventive":"total_Periodic" ,
                "Major" :"total_major",
                "Minor":"total_minor",
                "Actual Material Expenses": "material_expenses",
                "Actual Service Expenses": "service_expenses",
                "Total Expenses": "total_expenses",
            },
            json_meta: [
                [
                    {
                        key: "charset",
                        value: "utf-8",
                    },
                ],
            ],
        };
    },
    methods: {
        async get_tickets(ticket,x) {
            this.sub_tickets =''
            let arr = {}

            arr.machine_id=this.search.machine_id,
            arr.site_id=this.search.site_id
            arr.category_id=this.search.category_id
            arr.sub_category_id=this.search.sub_category_id
            arr.date_from=this.search.date_from
            arr.date_to=this.search.date_to
            arr.report_type=this.search.report_type

            if (this.search.report_type == 'machine wise') {
                arr.machine_id = ticket.id
            }
            if (this.search.report_type == 'site wise') {
                arr.site_id = ticket.id
            }
            axios.post("/api/reports_child", arr)
                .then(res => {
                   this.sub_tickets = res.data.data
                })
                .catch(err => {
                    console.error(err);
                })
        },
        showClearText() {
            this.clearEmittedText = 'emitted'
            this.default_month = ''
            window.setTimeout(() => {
                this.clearEmittedText = null
            }, 1000)
        },
        async get_machine() {
            axios
                .get("/api/reports/machines", {
                    params: {
                        site_id: this.search.site_id,
                        category_id: this.search.category_id,
                        sub_category_id: this.search.sub_category_id,
                    },
                })
                .then((response) => {
                    this.search_machines = response.data.data;
                })
                .catch(() => console.warn("Oh. Something went wrong"));
        },
        reset_filter() {
            this.search = {
                page: 1,
                machine_id: "",
                site_id: "",
                category_id: "",
                sub_category_id: "",
                monthYear: "",
                report_type: "machine wise",
            };
            this.getResults();
        },
        UpdateValues(e) {
            this.search.tat_from = e.minValue;
            this.search.tat_to = e.maxValue;
        },
        // excel
        async export_csv() {
            this.search.report_type = this.search.report_type == 'machine wise' ? 'machine wise export' : 'site wise export'
            const response = await axios.post("api/reports", this.search);
            this.search.report_type = this.search.report_type == 'machine wise export' ? 'machine wise' : 'site wise'
            return response.data.data;
        },
        startDownload() {
            Swal.fire({
                title: "Please Wait !",
                html: "Data populating",
                allowOutsideClick: false,
                onBeforeOpen: () => {
                    Swal.showLoading();
                },
            });
        },
        finishDownload() {
            Swal.close();
        },

        getLabel(val) {
            if (typeof val === "object") {
                return val.far_no + " - " + val.name;
            } else {
                return val;
            }
        },
        getResults(page = 1) {
            this.$Progress.start();
            this.sub_tickets=''
            this.sub_index=''
            this.search.page = page;

            axios.post("/api/reports", this.search)
                .then(res => {
                    this.Tickets = res.data.data
                })
                .catch(err => {
                    console.error(err);
                })
            this.$Progress.finish();
        },

    },
    created() {
        this.getResults();
    },
    beforeCreate() {
        axios.get("api/get_pre_machine").then((res) => {
            this.categories = res.data.data.Category;
            this.sites = res.data.data.sites;
        });
        axios
            .get("/api/initial_ticket")
            .then((response) => {
                this.users = response.data.data.vendors;
                this.machines = response.data.data.machines;
                this.search_machines = response.data.data.machines;
                this.search_sites = response.data.data.sites;
                this.created_users = response.data.data.created_users;
            })
            .catch(() => console.warn("Oh. Something went wrong"));
    },
    watch: {
        async "search.sub_category_id"(n, o) {
            if (n) {
                this.get_machine();
            }
        },
        async "search.site_id"(n, o) {
            if (n) {
                this.get_machine();
            }
        },

        "search.category_id": {
            handler: function (n, o) {
                if (n) {
                    axios.get("api/category/" + n).then((res) => {
                        this.sub_categories = res.data.data;
                    });
                    this.get_machine();
                }
            },
            deep: true,
            initial: true,
        },
    },


};





</script>
